.logo {
  max-width: max-content;
}

.logo__image {
  width: 136px;
  max-width: max-content;
  height: 32px;
  display: block;
}

.icon {
  width: 24px;
  height: 24px;
  transition: transform .5s;
}

.icon:hover {
  transform: scale(1.15);
}

.icon--menu {
  position: relative;
}

.icon--menu:before, .icon--menu:after {
  content: "";
  background-color: #1c1a26;
  width: 100%;
  height: 2px;
  transition: transform .5s, top .5s, opacity .5s;
  display: block;
  position: absolute;
  left: 0;
}

.icon--menu:before {
  top: 6px;
}

.icon--menu:after {
  bottom: 6px;
}

.icon--close {
  position: relative;
}

.icon--close:before, .icon--close:after {
  content: "";
  background-color: #1c1a26;
  width: 100%;
  height: 2px;
  transition: transform .5s, top .5s, opacity .5s;
  display: block;
  position: absolute;
  left: 0;
}

.icon--close:before {
  transform-origin: center;
  top: 50%;
  transform: rotate(45deg)rotateY(180deg);
}

.icon--close:after {
  transform-origin: center;
  top: 50%;
  transform: rotate(-45deg)rotateX(-180deg);
}

@media (min-width: 1200px) {
  .icon {
    display: none;
  }
}

.header__content {
  --header-top-padding: 16px;
  --header-bottom-padding: 104px;
  padding: var(--header-top-padding) 0 var(--header-bottom-padding);
